ul {
  margin: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 0 5px #616070;
}

.header__logo-link {
  width: 40px;
  height: 40px;
  margin: 10px 0;
}

.header__nav-list {
  justify-content: space-between;
  padding: 19px 0;
  list-style: none;
  display: flex;
}

.header__nav-link {
  color: #060b35;
  text-transform: uppercase;
  margin: 0 10px;
  padding: 23px 0;
  font-size: 12px;
  line-height: 22px;
  text-decoration: none;
  transition: color .3s, transform .3s;
}

.header__nav-link:hover {
  color: #00acdc;
}

.header__nav-item:first-child .header__nav-link {
  margin: 0 10px 0 0;
}

.header__nav-item:last-child .header__nav-link {
  margin: 0 0 0 10px;
}

.is-active {
  color: #00acdc;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  top: 56px;
  left: 0;
}

.container {
  grid-template-columns: repeat(4, 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width <= 1023px) {
  .container {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width <= 767px) {
  .container {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width <= 487px) {
  .container {
    grid-template-columns: 200px;
  }
}

.card {
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  transition: transform .3s, color .3s;
}

.card:hover {
  transform: scale(1.2);
}

.card__title {
  color: #060b35;
  margin: 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card:hover .card__title {
  color: #34568b;
}

.card__image {
  width: 160px;
  height: 134px;
  padding: 32px 19px 36px;
}

.card__info {
  padding: 0 16px 16px;
}

.card__subtitle {
  color: #616070;
  margin: 0;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__subtitle--margin-top {
  margin-top: 4px;
}

.card__rating {
  justify-content: space-between;
  align-items: center;
